<template>
  <v-app-bar absolute flat height="75">
    <template v-slot:prepend>
      <v-btn
        class="mr-3"
        elevation="1"
        color="secondary"
        size="small"
        @click="setDrawer()"
        :icon="icon"
      >
      </v-btn>
    </template>
    <v-app-bar-title class="hidden-sm-and-down font-weight-light"
      ><strong>{{ $route.name }}</strong></v-app-bar-title
    >
    <template v-slot:append>
      <!-- <div class="d-flex align-center">
        <span class="mr-4">{{ me?.username }}</span>
        <v-chip class="mr-4" :color="roleColor(me?.role)" variant="flat">{{
          me?.role
        }}</v-chip>
      </div> -->
      <v-menu v-model="menu" :close-on-content-click="false" location="bottom">
        <template v-slot:activator="{ props }">
          <v-btn v-bind="props" variant="plain" append-icon="mdi-dots-vertical">
            {{ me?.username }}
          </v-btn>
        </template>
        <v-card width="auto">
          <v-list-item :title="me?.username">
            <template v-slot:append>
              <v-chip
                class="ml-2"
                size="small"
                :color="roleColor(me?.role)"
                variant="flat"
                >{{ me?.role }}</v-chip
              >
            </template>
          </v-list-item>
          <v-divider></v-divider>
          <v-list nav dense>
            <v-list-item prepend-icon="mdi-account-outline" @click="openDetail">
              Conta</v-list-item
            >
            <!-- <v-list-item prepend-icon="mdi-key-variant"
              >Mudar password</v-list-item
            > -->
            <ChangePassword :agent_id="me?.id" />
            <!-- <v-list-item prepend-icon="mdi-cog-outline">
              Configurações</v-list-item
            > -->
            <v-list-item prepend-icon="mdi-logout" @click="logout()"
              >Sair</v-list-item
            >
          </v-list>
        </v-card>
      </v-menu>
    </template>
    <!-- <div class="d-flex align-center justify space between"></div> -->
    <AgentDetail
      :me="me?.id"
      :editMode="true"
      :dialog="detailDialog"
      @update:dialog="(value) => (detailDialog = value)"
    />
  </v-app-bar>
</template>

<script>
import AgentDetail from "./Agents/AgentDetail.vue";
import ChangePassword from "./Agents/ChangePassword.vue";

import { useGeneralStore } from "@/stores/generalStore";
import { useAccountStore } from "@/stores/accountStore";

export default {
  name: "AppBar",
  components: {
    AgentDetail,
    ChangePassword,
  },
  setup() {
    const generalStore = useGeneralStore();
    const accountStore = useAccountStore();
    return { generalStore, accountStore };
  },
  data() {
    return {
      menu: false,
      detailDialog: false,
    };
  },
  computed: {
    icon() {
      let _drawer = this.generalStore.navDrawerOpen;
      return _drawer ? "mdi-arrow-collapse-left" : "mdi-arrow-expand-right";
    },
    me() {
      return this.accountStore.getAccount();
    },
  },
  methods: {
    openDetail() {
      this.detailDialog = true;
    },
    logout() {
      this.accountStore.resetAccount();
      this.$router.push("/login");
    },
    setDrawer() {
      let _drawer = this.generalStore.navDrawerOpen;
      this.generalStore.navDrawerOpen = !_drawer;
    },
    roleColor(role) {
      switch (role) {
        case "Administrador":
          return "error";
        case "Organização":
          return "warning";
        case "Produtor":
          return "success";
        case "Agente de Campo":
          return "info";
        default:
          return "primary";
      }
    },
  },
};
</script>

<style></style>
