<template>
  <v-dialog v-model="dialog" @afterLeave="reset()" persistent width="auto">
    <template v-slot:activator="{ props: activatorProps }">
      <v-list-item prepend-icon="mdi-key-variant" v-bind="activatorProps"
        >Mudar password</v-list-item
      >
    </template>
    <template v-slot:default="{}">
      <v-card width="500">
        <v-card-title class="bg-warning d-flex align-center">
          <v-icon size="small" class="mr-2">mdi-key-variant</v-icon>
          <span>Mudar password</span>
        </v-card-title>
        <v-card-text>
          <v-row class="mt-2" justify="center">
            <v-col cols="10"
              ><v-form v-model="formValid">
                <v-row>
                  <v-col cols="12">
                    <v-text-field
                      v-model="old_password"
                      :rules="[rules.required, rules.maxLength]"
                      :append-inner-icon="
                        oldVisible ? 'mdi-eye-off' : 'mdi-eye'
                      "
                      :type="oldVisible ? 'text' : 'password'"
                      label="Senha Atual"
                      placeholder="Insira a senha atual"
                      prepend-inner-icon="mdi-lock"
                      color="secondary"
                      density="compact"
                      variant="outlined"
                      @click:append-inner="oldVisible = !oldVisible"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12">
                    <v-text-field
                      v-model="new_password"
                      :rules="[rules.required, rules.maxLength]"
                      :append-inner-icon="
                        newVisible ? 'mdi-eye-off' : 'mdi-eye'
                      "
                      :type="newVisible ? 'text' : 'password'"
                      label="Nova Senha"
                      placeholder="Insira a nova senha"
                      prepend-inner-icon="mdi-lock-plus"
                      color="secondary"
                      density="compact"
                      variant="outlined"
                      @click:append-inner="newVisible = !newVisible"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12">
                    <v-text-field
                      v-model="confirm_password"
                      :rules="[rules.required, rules.maxLength]"
                      :append-inner-icon="
                        confirmVisible ? 'mdi-eye-off' : 'mdi-eye'
                      "
                      :type="confirmVisible ? 'text' : 'password'"
                      label="Confirmar Senha"
                      placeholder="Confirme a nova senha"
                      prepend-inner-icon="mdi-lock-check"
                      color="secondary"
                      density="compact"
                      variant="outlined"
                      @click:append-inner="confirmVisible = !confirmVisible"
                    ></v-text-field>
                  </v-col>
                </v-row> </v-form
            ></v-col>
          </v-row>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions class="d-flex justify-space-around">
          <v-btn
            color="error"
            variant="tonal"
            icon="mdi-close"
            @click="dialog = false"
          ></v-btn>
          <v-btn
            color="secondary"
            variant="tonal"
            icon="mdi-check"
            :loading="isLoading"
            :disabled="!formValid"
            @click="postChangePassword()"
          ></v-btn>
        </v-card-actions>
      </v-card>
    </template>
  </v-dialog>
</template>

<script>
import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";
export default {
  name: "ChangePassword",
  props: {
    agent_id: {
      type: Number,
      required: true,
    },
  },
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();

    return { accountStore, generalStore };
  },
  data() {
    return {
      dialog: false,
      isLoading: false,

      old_password: "",
      new_password: "",
      confirm_password: "",
      rules: {
        required: (value) => !!value || "Campo obrigatório",
        maxLength: (value) => value.length <= 40 || "Máximo de 40 caracteres",
        passwordMatch: (value) =>
          value === this.new_password || "Passwords não coincidem",
      },
      formValid: false,

      oldVisible: false,
      newVisible: false,
      confirmVisible: false,
    };
  },
  watch: {
    dialog(val) {
      if (val) {
        this.onOpenDialog();
      }
    },
  },
  methods: {
    onOpenDialog() {},
    reset() {
      this.old_password = "";
      this.new_password = "";
      this.confirm_password = "";
    },
    async postChangePassword() {
      if (this.new_password !== this.confirm_password) {
        this.generalStore.errorSnackbar("Passwords não coincidem");
        return;
      }
      this.isLoading = true;
      await axios
        .request({
          url: "/api/agents/change_password",
          method: "patch",
          headers: {
            Authorization: `Bearer ${this.accountStore.account.token}`,
          },
          data: {
            old_password: this.old_password,
            new_password: this.new_password,
          },
        })
        .then(() => {
          this.isLoading = false;
          this.dialog = false;
          this.generalStore.successSnackbar("Password alterada com sucesso");
        })
        .catch((error) => {
          this.isLoading = false;
          this.generalStore.errorSnackbar(error.response.data.error);
        });
    },
  },
};
</script>

<style></style>
