<template>
  <v-navigation-drawer v-model="generalStore.navDrawerOpen" color="primary">
    <v-list-item @click="goHome">
      <v-img
        src="../assets/fitoagro_logo.png"
        class="ma-2"
        height="5rem"
      ></v-img>
    </v-list-item>
    <v-divider></v-divider>
    <v-list nav dense>
      <template v-for="item in navItems">
        <v-list-item
          :key="item.title"
          v-if="!item.children"
          :prepend-icon="item.icon"
          :to="item.to"
          exact
        >
          {{ item.title }}
        </v-list-item>
        <v-list-group v-else :prepend-icon="item.icon">
          <template v-slot:activator="{ props }">
            <v-list-item v-bind="props">{{ item.title }}</v-list-item>
          </template>
          <v-list-item
            v-for="child in item.children"
            :key="child.title"
            :prepend-icon="child.icon"
            :to="child.to"
            exact
          >
            {{ child.title }}</v-list-item
          >
        </v-list-group>
      </template>
    </v-list>
    <!-- <template v-slot:append>
      <div class="pa-2">
        <v-btn
          block
          variant="text"
          prepend-icon="mdi-logout"
          size="large"
          @click="logout()"
        >
          Sair</v-btn
        >
      </div>
    </template> -->
  </v-navigation-drawer>
</template>

<script>
import { useGeneralStore } from "@/stores/generalStore";
import { useAccountStore } from "@/stores/accountStore";

export default {
  setup() {
    const generalStore = useGeneralStore();
    const accountStore = useAccountStore();
    return { generalStore, accountStore };
  },
  data() {
    return {
      drawer: true,
      // navItems: [
      //   { title: "Dashboard", icon: "mdi-view-dashboard", to: "/" },
      //   {
      //     title: "Meteorologia",
      //     icon: "mdi-weather-partly-cloudy",
      //     children: [
      //       {
      //         title: "Estações",
      //         icon: "mdi-tower-fire",
      //         to: "/weather_stations",
      //       },
      //       { title: "Previsão", icon: "mdi-layers", to: "/forecast" }, // assuming you have a route for forecast
      //       {
      //         title: "Fornecedores",
      //         icon: "mdi-account-group",
      //         to: "/providers",
      //       },
      //     ],
      //   },
      //   {
      //     title: "Parcelas",
      //     icon: "mdi-land-plots-marker",
      //     to: "/regions",
      //   },
      //   {
      //     title: "Utilizadores",
      //     icon: "mdi-account-group",
      //     to: "/users",
      //   },
      // ],
    };
  },
  computed: {
    navItems() {
      let _items = [
        // { title: "Home", icon: "mdi-home", to: "/" },
        {
          title: "Meteorologia",
          icon: "mdi-weather-partly-cloudy",
          children: [
            {
              title: "Estações",
              icon: "mdi-tower-fire",
              to: "/weather_stations",
            },
          ],
        },
        {
          title: "Parcelas",
          icon: "mdi-land-plots-marker",
          to: "/regions",
        },
        {
          title: "Períodos",
          icon: "mdi-calendar-multiple",
          to: "/periods",
        },
        {
          title: "Organizações",
          icon: "mdi-domain",
          to: "/organizations",
        },
      ];

      if (this.accountStore.hasRole(["Administrador", "Agente de Campo"])) {
        _items.push({
          title: "Visitas",
          icon: "mdi-walk",
          to: "/visits",
        });
        _items.push({
          title: "Protocolos",
          icon: "mdi-file-document",
          to: "/protocols",
        });
      }
      if (this.accountStore.hasRole(["Administrador", "Organização"])) {
        _items.push({
          title: "Utilizadores",
          icon: "mdi-account-group",
          to: "/users",
        });
      }
      if (this.accountStore.hasRole(["Administrador"])) {
        _items[0].children.push({
          title: "Fornecedores",
          icon: "mdi-account-group",
          to: "/providers",
        });
        _items.push({
          title: "Sistema",
          icon: "mdi-cogs",
          to: "/system",
        });
      }
      return _items;
    },
  },
  methods: {
    goHome() {
      this.$router.push("/");
    },
    logout() {
      this.accountStore.resetAccount();
      this.$router.push("/login");
    },
  },
};
</script>

<style></style>
